<template>
    <div class="vh-animate vh-animate-fade-in vh-text-center">
        <div class="vh-card vh-card-body vh-card-default vh-card-small">
            <div class="vh-margin-top">
                <small class="axs-small">
                    TICKET SERIES NO.
                </small>

                <h2 class="axs-series-number vh-margin-small-top">
                    {{ getNo('starting')}} ~ {{ getNo('ending')}}
                </h2>
            </div>

            <div class="vh-margin-medium-top">
                <small class="axs-small">
                    TOTAL TICKETS
                </small>

                <h2 class="axs-series-number vh-margin-small-top vh-margin-remove-bottom">
                    {{ getNo('total')}}
                </h2>
            </div>

            <div class="vh-margin-top">
                <vh-button
                type="button"
                :size="'small'"
                :color="'warning'"
                @click="toggleModal('form', true)">
                EDIT
            </vh-button>
            </div>
        </div>

        <div class="vh-margin-medium-top">
            <small class="axs-small">
                LAST RELEASED TICKET NO.
            </small>

            <h3 class="axs-release-number vh-margin-small-top">
                <span v-if="hasRelease">{{ getNo('last_release') }}</span>
                <span v-else class="vh-text-italic">No released ticket yet</span>
            </h3>
        </div>

        <div class="vh-margin-small-top">
            <small class="axs-small">
                NEXT TICKET NO.
            </small>
            <h3 class="axs-release-number vh-margin-small-top">
                {{ nextRelease }}
            </h3>
        </div>

        

        <div class="vh-margin-top">
            <vh-button
                v-if="hasRelease"
                class="vh-inline vh-margin-small-right"
                type="button"
                :size="'small'"
                :color="'danger'"
                :outline="true"
                @click="undoRelease()">
                UNDO {{ getNo('last_release')}}
            </vh-button>
            <vh-button
                v-show="getNo('last_release') == null || parseInt(getNo('last_release')) < parseInt(getNo('ending'))"
                class="vh-inline"
                type="button"
                :size="'small'"
                :color="'primary'"
                @click="release()">
                RELEASE {{ nextRelease }}
            </vh-button>
        </div>

        <form-modal />
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

import { Toast } from '@/utils';

import FormModal from './modals/form';

export default
{
    name: 'Tickets',

    components:
    {
        FormModal,
    },

    data()
    {
        return {
            selected: {},

            formModal: false,
        };
    },

    created()
    {
        this.initialize();
    },

    computed:
    {
        ...mapGetters({
            getNo: 'auth/getSeriesNo',
            isSet: 'auth/isSet',
        }),

        hasRelease()
        {
            return this.getNo('last_release');
        },

        nextRelease()
        {
            if (this.hasRelease)
            {
                return parseInt(this.getNo('last_release')) +1;
            }
            else
            {
                return this.getNo('starting');
            }
        },

        isTicketEnoughForRelease()
        {
            return parseInt(this.nextRelease) <= parseInt(this.getNo('ending'));
        },

        isTicketForUndo()
        {
            return this.hasRelease && parseInt(this.getNo('last_release')) >= parseInt(this.getNo('starting'));
        }
    },

    methods:
    {
        ...mapActions({
            update: 'auth/updateLastRelease',
        }),

        async release()
        {
            if (this.isTicketEnoughForRelease)
            {
                await this.update(this.nextRelease);
            }
            else
            {
                Toast.error({
                    message: 'Oops, ticket is not enough for releasing.',
                });
            }
        },

        async undoRelease()
        {
            if (this.isTicketForUndo)
            {
                if (parseInt(this.getNo('last_release')) > parseInt(this.getNo('starting')))
                {
                    await this.update(parseInt(this.getNo('last_release') -1));
                }
                else
                {
                    await this.update(null);
                }
                
            }
            else
            {
                Toast.error({
                    message: 'Oops, no released ticket yet.',
                });
            }
        },

        initialize()
        {
            if (!this.isSet)
                this.toggleModal('form', true);
        },

        toggleModal(name, bool)
        {
            this[`${name}Modal`] = bool;
        },
    }
};
</script>
